<template>
  <div>
    <div class="chat_window">
      <div class="top_menu">
        <div class="buttons">
          <div class="button close">
          </div>
          <div class="button minimize">
          </div>
          <div class="button maximize">
          </div>
        </div>
        <div class="title">
          Chat
        </div>
      </div>
      <chatlog :chatlog="chatlog"></chatlog>
      <div class="bottom_wrapper clearfix">
        <div class="message_input_wrapper">
          <input class="message_input" v-model="message_text" v-on:keyup.13="sendMessage" placeholder="Type your message here..." />
        </div>
        <div class="send_message">
          <div class="icon">
          </div>
          <a v-on:click="sendMessage">
            <span class="text" style="display: block">
              Send
            </span>
          </a>
        </div>
      </div>
    </div>
    <div class="message_template">
      <li class="message">
        <div class="avatar">

        </div>
        <div class="text_wrapper">
          <div class="text">

          </div>
        </div>
      </li>
    </div>
  </div>
</template>
<script>
  import chatlog from './ChatLog.vue'

  export default {
    data: function(){
      return {
        message_text:'',
        sockets:{
          connect: function(){
            console.log('socket connected')
          },
        },
      }
    },
    methods: {
      sendMessage: function(){
        if(this.message_text==''){
          return false;
        }
        var message = {
          message_text: this.message_text,
          sender: 'You'
        }
        this.chatlog.push(message);
        this.message_text = '';
        this.$socket.emit('emit_method', message);
      }
    },
    name:'chatbox',
    props:['chatlog'],
    created: function () {
      console.log(this.chat_box);
    },
    mounted: function() {
      console.log('Component mounted.')
    }
  }
</script>



